    ]\r\n              },\r\n          ]\r\n        }\r\n    },\r\n    methods:{\r\n      \r\n    }\r\n}\r\n</script>\r\n<style>\r\n    h2 {\r\n        text-align: center;\r\n    }\r\n    h3 {\r\n        text-align: center;\r\n        color:dimgrey;\r\n        font-size:18px;\r\n        margin-top: 40px;\r\n    }\r\n    h4 {\r\n        text-align: center;\r\n        color:red;\r\n    }\r\n    \r\n</style>"],"sourceRoot":""}]);

